.slider-basket {
  margin-top: 20px; }
  .slider-basket * {
    box-sizing: border-box !important; }
  .slider-basket__title {
    margin-bottom: 10px;
    font-size: 16px;
    color: #232833; }
  .slider-basket__wrapper {
    position: relative;
    padding: 0 35px; }
  .slider-basket__slide {
    display: none; }
    .slider-basket__slide:first-child {
      display: block; }
  .slider-basket__prev, .slider-basket__next {
    position: absolute;
    z-index: 10;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 2.2em;
    height: 2.2em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border: 0;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    border-radius: 50%;
    font-size: 16px;
    cursor: pointer;
    opacity: 0.9; }
    .slider-basket__prev.slick-disabled, .slider-basket__next.slick-disabled {
      opacity: 0; }
  .slider-basket__prev {
    left: 0; }
  .slider-basket__next {
    right: 0; }
  .slider-basket__group {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px; }
  .slider-basket__item {
    flex: 0 0 auto;
    width: 50%;
    padding: 10px 10px;
    display: flex; }
  .slider-basket__image {
    flex: 0 0 auto;
    width: 45px;
    height: 45px; }
  .slider-basket__desc {
    flex: 0 0 auto;
    width: calc(100% - 45px);
    padding-left: 15px;
    display: flex;
    flex-direction: column; }
  .slider-basket__name {
    margin-bottom: auto;
    font-size: 13px; }
    .slider-basket__name a {
      color: inherit; }
  .slider-basket__ctrl {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .slider-basket .slick-slide {
    display: block !important; }
  .slider-basket .stepper {
    margin-left: 0 !important; }

.header__bottom-cart {
  cursor: default !important; }
